ar muy bien el número de animales pesados para asegurar que los datos no sean erróneos, todos los resultados, se anotan en una hoja de control o registro para procesar los datos, analizarlos y tomar decisiones de acuerdo con la información obtenida. Los animales con bajo peso o enfermos se deben descartar.")]),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("El pesaje inicia desde el momento de la recepción de las aves en el galpón, este se realiza escogiendo al azar como mínimo el 10 % de las cajas que contienen los pollitos o pollitas, se pesan, se descuenta el peso de la caja (peso sin animales), se cuenta la cantidad de animales y se consignan los datos en la hoja de registro.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"zoom-in"}},[_vm._v("Existen dos técnicas para el pesaje de aves: ")]),_vm._m(7),_vm._m(8),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("Se debe cuidar de utilizar la misma báscula en cada pesaje, realizar mantenimiento periódico y calibrar previo a cada pesaje.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"zoom-in"}},[_vm._v("Esta técnica de pesaje sirve para hallar el peso promedio de la parvada y se realiza de la siguiente manera: utilizando un corral de captura, se seleccionan como mínimo 100 aves o el 1 % de la población del galpón (lo que sea mayor) para realizar el pesaje. Este procedimiento se realiza tomando población de aves de tres sitios diferentes del galpón, cuidando siempre de hacerlo de manera proporcional y que sea lejos de las puertas y paredes del galpón.")]),_vm._m(9),_c('p',{attrs:{"data-aos":"zoom-in"}},[_vm._v("En la figura se pueden observar los círculos rojos que indican el ejemplo de dónde se deben tomar las muestras.")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"zoom-in"}},[_vm._v("Si las aves están en las primeras semanas se pueden pesar varias en un recipiente como un balde o bandeja, teniendo en cuenta que no deben apilarse (uno encima de otro) o maltratar los animales. La capacidad puede variar de 10 a 2